const uni = require('uni').default;





































import pageHead from '../../components/page-head.vue'
export default {
	data() {
		return {
			title: '标签页',
			scrollLeft: 0,
			isClickChange: false,
			currentTab: 0,
			forcedLogin:false,
			hasLogin:false,
			tabs: [{
				name: '关注',
				id: 'guanzhu'
			}, {
				name: '推荐',
				id: 'tuijian'
			}, {
				name: '体育',
				id: 'tiyu'
			}, {
				name: '热点',
				id: 'redian'
			}, {
				name: '财经',
				id: 'caijing'
			}],
			newsitems: []
		}
	},
	components:{
		pageHead
	},
	onLoad: function () {
		this.newsitems = this.randomfn();
		uni.getStorage({
			key: 'cc',
			success: (res) => {
				this.hasLogin = true;
			},
			fail: () => {
				console.log('读取数据失败 找不到 key 对应的数据');
			}
		})
		if (!this.hasLogin) {
			var self = this;
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				* 如果需要强制登录，不显示取消按钮
				*/
				showCancel: !self.forcedLogin,
				success: function (res) {
					if (res.confirm) {
						/**
						* 如果需要强制登录，使用reLaunch方式
						*/
						if (self.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
	},
	onUnload:function(){
		this.scrollLeft = 0,
		this.isClickChange = false,
		this.currentTab = 0;
	},
	methods: {
		bindChange: async function (e) {
			let index = e.target.current;
			if (this.isClickChange) {
				this.currentTab = index;
				this.isClickChange = false;
				return;
			}
			let tabBar = await this.getWidth("tab-bar"),
				tabBarScrollLeft = tabBar.scrollLeft;

			let width = 0;

			for (let i = 0; i < index; i++) {
				let result = await this.getWidth(this.tabs[i].id);
				width += result.width;
			}

			let winWidth = uni.getSystemInfoSync().windowWidth,
				nowElement = await this.getWidth(this.tabs[index].id),
				nowWidth = nowElement.width;

			if (width + nowWidth - tabBarScrollLeft > winWidth) {
				this.scrollLeft = width + nowWidth - winWidth;
			}
			if (width < tabBarScrollLeft) {
				this.scrollLeft = width;
			}
			this.isClickChange = false;
			this.currentTab = index; //一旦访问data就会出问题
		},
		getWidth: function (id) { //得到元素的宽高
			return new Promise((res, rej) => {
				uni.createSelectorQuery().select("#" + id).fields({
					size: true,
					scrollOffset: true
				}, (data) => {
					if (id === 'tab-bar') {
						// console.log("id="+ id+",数据:"+JSON.stringify(data));
					}
					res(data);
				}).exec();
			})
		},
		swichNav: async function (e) { //点击tab-bar
			if (this.currentTab === e.target.dataset.current) {
				return false;
			} else {
				let tabBar = await this.getWidth("tab-bar"),
					tabBarScrollLeft = tabBar.scrollLeft; //点击的时候记录并设置scrollLeft
				this.scrollLeft = tabBarScrollLeft;
				this.isClickChange = true;
				this.currentTab = e.target.dataset.current
			}
		},
		loadMore: function (e) {
			let last = this.newsitems[e][this.newsitems[e].length - 1].label,
				name = this.newsitems[e][this.newsitems[e].length - 1].name,
				imgurl = this.newsitems[e][this.newsitems[e].length - 1].img;
			for (let i = 1; i <= 10; i++) {
				this.newsitems[e].push({
					name: name,
					label: name+'第'+(i + parseInt(last.substr(last.indexOf('第')+1,last.indexOf('第')+2)))+'个条目的详细内容!',
					img:imgurl
				});
			}
		},
		randomfn() {
			let ary = [];
			for (let i = 0, length = this.tabs.length; i < length; i++) {
				let aryItem = [];
				for (let j = 1; j <= 20; j++) {
					aryItem.push({
						name: this.tabs[i].name,
						label: this.tabs[i].name+'第'+j+'个条目的详细内容!',
						img:'../../static/imgs/tabbar/tabbar_'+(i+1)+'.png'
					});
				}
				ary.push(aryItem);
			}
			return ary;
		},
		show:function(obj){
			uni.showToast({
				icon:'none',
				title:'当前点击'+obj.name+'-'+obj.label,
			})
		}
	}
}



